import React from "react";
import {Link } from "react-router-dom";

function Home() {
    return (
        <div className="h-screen flex flex-col items-center justify-center text-white">
            <h1 className="text-3xl font-bold">Welcome Home</h1> <br />
            <div className="flex flex-row">
                <Link className="bg-gray-100 px-3 py-2 text-gray-900 rounded" activeClassName="active" to="/dashboard">Dashboard</Link>
            </div>
        </div>
    )
}

export default Home