import React, { useEffect, useState } from "react";
import API from "../../../asset/tools/api";

const Pendapatan = () => {

    
    const [Pendapatan, setPendapatan] = useState(null);
    const [PendatanAvg, setPendatanAvg] = useState(null);

    useEffect(() => {
        const dataInterval = setInterval( async () => {
            await API.get('keuangan/pendapatan')
                .then(res => {
                    const data = res.data
                    setPendatanAvg(data.totalavg)
                    setPendapatan(data.total)
                })              
        }, 15000);
        return () => clearInterval(dataInterval)
    })
    
    return (
        <div className="h-full w-full flex flex-col">
            <div className="h-1/2 w-full p-1">
                <div className="h-full p-1 bg-yellow-500 rounded flex flex-col items-center">
                    <div className="text-sm">Pendapatan total</div>
                    <div className="text-3xl font-bold">{Pendapatan}</div>
                </div>
            </div>
            <div className="h-1/2 w-full p-1">
                <div className="h-full p-1 bg-red-700 rounded flex flex-col items-center">
                    <div className="text-sm">Pendapatan total rata-rata</div>
                    <div className="text-3xl font-bold">{PendatanAvg}</div>
                </div>
            </div>
        </div>
    )
}

export default Pendapatan
