import { React } from "react";
import { DateTime } from "./dateTime";

function Navbar() {
    return <nav className="fixed bg-gray-700 top-0 inset-x-0 ">
        <div className=" px-3 py-2 w-full h-12 flex flex-row justify-between items-center">
            <div className="w-1/3 flex items-center">
                <div className="text-white text-base font-bold">SIMRS</div>
            </div>
            <div className="w-1/3 flex justify-center items-center">
                <div className="p-1">
                    <div className="text-white text-2xl font-bold"></div>
                </div>
            </div>
            <div className="w-1/3 flex flex-row-reverse items-center">
                <div className="p-1">
                    <div className="w-8 h-8 bg-blue-500 rounded-full"></div>
                </div>  
            </div>
        </div>
        <div className="bg-gray-700 px-3 py-1 w-full h-8 text-white flex flex-row justify-end items-center">
            <div className="mr-2">{`${DateTime()}`}</div>
            <button class="bg-gray-600 px-1 flex border border-gray-700 justify-center text-base material-icons-round rounded transition-colors hover:bg-gray-700 hover:border-gray-500 hover:text-white hover:shadow">search</button>
        </div>
    </nav> 
}

export default Navbar