import React, { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";
import API from "../../../asset/tools/api";

const TrendPendapatan = () => {

    const [pendapatan, setPendapatan] = useState(null);
    const [pendapatanBulan, setPendapatanBulan] = useState(null);
    const months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]


    useEffect(() => {
        const dataInterval = setInterval( async () => {
            await API.get('keuangan/trendPendapatan')
                .then(res => {
                    const data = res.data.result
                    const resultbulan = data.map(obj =>months[obj.bulan - 1])
                    const resultTotal = data.map(obj => obj.total)
                    setPendapatan(resultTotal)
                    setPendapatanBulan(resultbulan)
                })
                .catch(error => {
                        console.log(error);
                    }
                )
                
        }, 15000);
        return () => clearInterval(dataInterval)
    })

    const data = {
        labels: pendapatanBulan,
        datasets: [
            {
                label: "Rawat Jalan",
                data: pendapatan,
                fill: true,
                cubicInterpolationMode: 'monotone',
                backgroundColor: "rgba(75,192,192,0.2)",
                borderColor: "#93C5FD"
            },
        ]
    };
    
    const options= {
        plugins: {
            legend: {
                display: false,
                fontColor: "white"
            },
            title: {
              display: true,
              color: "white",
              text: 'Trend Pendapatan'
            }
        },
        responsive: true,
        maintainAspectRatio: false,
        animation: {
            duration: 0
        },
        scales: {
            x: {
                ticks: {
                    color: "white"
                },
                gridLines: {
                    display: false,
                    color: "white"
                },
            },
            y: {
                ticks: {
                    color: "white"
                }
            },
        }
    };
    return <Line data={data} options={options} />
}

export default TrendPendapatan